*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0 auto;
}

body {
  background-color: rgba(66, 59, 59, 0.227);
  line-height: 2;
  background-image: url("../img/computers-view.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.App {
  background-color: rgba(199, 193, 193, 0.555);
  width: 45rem;
  height: 28rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 17rem;
}

span,
.form {
  font-size: x-large;
  font-weight: bold;
}
